body {
  margin: 0;
  padding: 0;
  font-family: 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #242424;
  box-sizing: border-box;
}
.todo-container{
  padding: 20px;
  max-width: 400px;
}
.todo-container h2{
  color: white;
}

.todo-form input{
  width: 100%;
}
.todo-form input, .todo-form button, .todo-row button{
  min-height: 30px;
  padding: 0 10px;
  border: none;
  display: inline-block;
  font-size: 18px;
  outline: none;
}
.todo-form{
  display: flex;
  justify-content: space-between;
}
.todo-form button{
  min-height: 30px;
  /* margin-left: 10px; */
  cursor: pointer;
  min-width: 100px;
}
.todo-row{
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.todo-text{
  min-height: 30px;
  display: flex;
  align-items: center;
  width: 65%;
  padding-left: 10px;
}
.actions{
  width: 35%;
  display: flex;
  justify-content: stretch;
}
.actions button{
  font-size: 18px;
  cursor: pointer;
  width: 100%;
}
.actions button:first-child{
  background-color: cornflowerblue;
}
.actions button:last-child{
  background-color: red;
  color: white;
}
.todo-text{
  text-transform: capitalize;
}
.todo-text.complete{
  background-color: darkgreen;
  color: white;
}